*{
    font-family: 'Poppins,sans-serif';
}

#home{
    font-family: "Poppins,Sans-serif";
    margin: auto;
    width: 100%;
    height: 500px;
    overflow-x: hidden;
}

.back{
    width: 100%;
    height: 100%;
    -webkit-filter: blur(10px);
    filter: blur(10px);
}

#home .content{
    position: absolute;
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    margin: auto;
    margin-top: -25%;
    width: 100%;
    z-index: 2;
}

#about{
    /* background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 100%, rgba(0,212,255,1) 100%, rgba(1,187,239,1) 100%); */
    background-color: #23283E;
    width: 100%;
