@media  (min-width: 900px){
    #home{
        font-family: "Poppins,Sans-serif";
        margin: auto;
        width: 100%;
        height: 500px;
        overflow-x: hidden;
    }

    .name{
        font-size:80px; 
        font-weight:700; 
        line-height:100px; 
        color:#FFF;
    }
    
    .description{
        font-size:32px; 
        font-weight:700; 
        line-height:40px; 
        color:#FFF;
    } 

    .back{
        width: 100%;
        height: 100%;
        -webkit-filter: blur(10px);
        filter: blur(10px);
    }
    
    #home .content{
        position: absolute;
        justify-content: center;
        text-align: center;
        border-radius: 15px;
        margin: auto;
        margin-top: -25%;
        width: 100%;
        z-index: 2;
    }

    .dev{
        justify-content: center;
        display: flex;
        gap: 10px;
    }
}

@media  (min-width: 600px)  and (max-width: 899px) {
    #home{
        font-family: "Poppins,Sans-serif";
        margin: auto;
        width: 100%;
        height: 450px;
        overflow-x: hidden;
    }

    .name{
        font-size:64px; 
