        color:#FFF;
    }
    
    .description{
        font-size:32px; 
        font-weight:700; 
        line-height:40px; 
        color:#FFF;
    } 


    .back{
        width: 100%;
        height: 100%;
        -webkit-filter: blur(10px);
        filter: blur(10px);
    }
    
    #home .content{
        position: absolute;
        justify-content: center;
        text-align: center;
        border-radius: 15px;
        margin: auto;
