        gap: 10px;
    }

    
}
@media (max-width: 599px) {
    #home{
        font-family: "Poppins,Sans-serif";
        margin: auto;
        width: 100%;
        height: 350px;
        overflow-x: hidden;
    }

    .name{
        font-size:40px; 
        font-weight:600; 
        line-height:50px; 
        color:#FFF;
    }
    
    .description{
        font-size:24px; 
        font-weight:700; 
        line-height:40px; 
        color:#FFF;
    } 

    .back{
        width: 100%;
        height: 100%;
        -webkit-filter: blur(10px);
        filter: blur(10px);
    }
    
    #home .content{
        position: absolute;
        justify-content: center;
        text-align: center;
        border-radius: 15px;
        margin: auto;
        margin-top: -75%;
        width: 100%;
        z-index: 2;
    }

    .dev{
        width: 100%;
        justify-content: center;
        display: block;
        gap: 10px;
        padding: 10px;
    }
}